import React, { useEffect, useState } from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

const ExpenseForm = () => {
  const [date, setDate] = useState(null);
  const [itemName, setItemName] = useState("");
  const [amount, setAmount] = useState("");
  const [purchasedBy, setPurchasedBy] = useState("");
  const [savedData, setSavedData] = useState([]);
  const [filterBy, setFilterBy] = useState("");
  const [editIndex, setEditIndex] = useState(-1);
  const [filteredSum, setFilteredSum] = useState(0);

  useEffect(() => {
    fetch("http://expense.ankitrajmahapatra.co:200/get_expenses")
      .then((response) => {
        if (!response.ok) {
          throw new Error("Network response was not ok");
        }
        return response.json();
      })
      .then((data) => {
        // Update the savedData state with the received data
        setSavedData(data);
      })
      .catch((error) => {
        console.error("Error fetching data:", error);
      });
  }, []);

  const handleChange = (selectedDate) => {
    setDate(selectedDate);
  };

  const handleSaveData = () => {
    if (
      date == null ||
      itemName.trim() === "" ||
      amount.trim() === "" ||
      purchasedBy.trim() === ""
    ) {
      return;
    }

    const newData = {
      date: date,
      itemName: itemName,
      amount: amount,
      purchasedBy: purchasedBy,
    };

    if (editIndex !== -1) {
      // Editing an existing row
      const editedData = [...savedData];
      editedData[editIndex] = newData;
      setSavedData(editedData);
      setEditIndex(-1); // Reset edit index
    } else {
      // Adding a new row
      setSavedData((prevData) => [...prevData, newData]);
    }

    setDate(null);
    setItemName("");
    setAmount("");
    setPurchasedBy("");
  };

  const handleEditRow = (index) => {
    const dataToEdit = savedData[index];
    setDate(dataToEdit.date);
    setItemName(dataToEdit.itemName);
    setAmount(dataToEdit.amount);
    setPurchasedBy(dataToEdit.purchasedBy);
    setEditIndex(index);
  };

  const handleDeleteRow = (index) => {
    const newData = savedData.filter((_, i) => i !== index);
    setSavedData(newData);
  };

  return (
    <div className="container">
      <nav className="navbar bg-primary">
        <div className="container-fluid">
          <h1 className="navbar-brand">Room-Expense Tracker</h1>
        </div>
      </nav>
      <div className="input-field">
        <div className="row g-4">
          <div className="col-sm-6">
            <input
              type="text"
              className="form-control"
              placeholder="Item Name"
              value={itemName}
              onChange={(e) => setItemName(e.target.value)}
            />
          </div>
          <div className="col-sm">
            <input
              type="number"
              className="form-control"
              placeholder="Amount"
              aria-label="State"
              value={amount}
              onChange={(e) => setAmount(e.target.value)}
            />
          </div>
          <div className="col-sm">
            <select
              className="form-control"
              id="dropdownSelect"
              value={purchasedBy}
              onChange={(e) => setPurchasedBy(e.target.value)}
            >
              <option value="">Purchased By</option>
              <option value="Dhruv">Dhruv</option>
              <option value="Ankit">Ankit</option>
              <option value="Ayush">Ayush</option>
              <option value="Bikash">Bikash</option>
              <option value="New">New</option>
            </select>
          </div>
          <div className="col-sm">
            <DatePicker
              className="form-control"
              selected={date}
              onChange={handleChange}
              placeholderText="Purchase Date"
            />
          </div>
          <div className="col-sm">
            <button
              className="btn btn-success form-control"
              onClick={handleSaveData}
            >
              Save
            </button>
          </div>
        </div>
      </div>
      {savedData.length > 0 && (
        <div>
          <h3>Expenses</h3>
          <div>
            <input
              className="purchaser-filter"
              type="text"
              placeholder="Filter Purchaser By Name"
              value={filterBy}
              onChange={(e) => setFilterBy(e.target.value)}
            />
          </div>
          <table>
            <thead>
              <tr>
                <th>Date</th>
                <th>Item Name</th>
                <th>Amount</th>
                <th>Purchased By</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {savedData
                .filter(
                  (data) =>
                    filterBy === "" || data.purchasedBy.includes(filterBy)
                )
                .map((data, index) => (
                  <tr key={index}>
                    <td>
                      {data.date.toString().split(" ").slice(0, 4).join(" ")}
                    </td>
                    <td>{data.itemName}</td>
                    <td>{data.amount}</td>
                    <td>{data.purchasedBy}</td>
                    <td>
                      <button
                        className="action"
                        onClick={() => handleEditRow(index)}
                      >
                        Edit
                      </button>
                      <button
                        className="action"
                        onClick={() => handleDeleteRow(index)}
                      >
                        Delete
                      </button>
                    </td>
                  </tr>
                ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
};

export default ExpenseForm;
